import { css, html, LitElement, TemplateResult } from 'lit';
import { query } from 'lit/decorators.js';

import '@material/mwc-button';
import '@material/mwc-dialog';
import '@material/mwc-list';
import '@material/mwc-list/mwc-radio-list-item';
import '@material/mwc-textfield';
import type { Dialog } from '@material/mwc-dialog';
import type { TextField } from '@material/mwc-textfield';

import { newOpenEvent } from '@openscd/open-scd-core';
import { ListItemBase } from '@material/mwc-list/mwc-list-item-base';

type SchemaAttributes =
  | { version: ''; revision: ''; release: '' }
  | { version: '2007'; revision: 'B'; release: '' }
  | { version: '2007'; revision: 'B'; release: '4' };

const supportedAttributes: Record<string, SchemaAttributes> = {
  '2003': { version: '', revision: '', release: '' },
  '2007B': { version: '2007', revision: 'B', release: '' },
  '2007B4': { version: '2007', revision: 'B', release: '4' },
};

/** Returns a new empty SCD document. */
export function newEmptySCD(id: string, versionId: string): XMLDocument {
  const { version, revision, release } = supportedAttributes[versionId];
  const markup = `<?xml version="1.0" encoding="UTF-8"?>
    <SCL xmlns="http://www.iec.ch/61850/2003/SCL" ${
      version ? `version="${version}"` : ''
    } ${revision ? `revision="${revision}"` : ''} ${
    release ? `release="${release}"` : ''
  }>
      <Header id="${id}"/>
    </SCL>`;
  return new DOMParser().parseFromString(markup, 'application/xml');
}

export default class SclEditorLanding extends LitElement {
  @query('input')
  input!: HTMLInputElement;

  @query('mwc-dialog')
  newDialog!: Dialog;

  @query('mwc-list')
  versions!: Dialog;

  @query('#newProjectName')
  newProjectName!: TextField;

  async openDoc(event: Event): Promise<void> {
    const file = (<HTMLInputElement | null>event.target)?.files?.item(0);
    if (!file) return;

    const text = await file.text();
    const docName = file.name;
    const doc = new DOMParser().parseFromString(text, 'application/xml');

    this.dispatchEvent(newOpenEvent(doc, docName));
    this.input.onchange = null;
  }

  private createNewProject(): void {
    const docName = this.newProjectName.value?.match(/\.s[sc]d$/i)
      ? this.newProjectName.value
      : `${this.newProjectName.value}.scd`;

    const version = (<ListItemBase>(
      this.newDialog.querySelector('mwc-list')!.selected
    )).value;

    this.dispatchEvent(
      newOpenEvent(newEmptySCD(docName.slice(0, -4), version), docName)
    );
  }

  private renderNewDialog(): TemplateResult {
    return html`<mwc-dialog heading="New Project">
      <mwc-textfield
        id="newProjectName"
        label="name"
        value="project.scd"
        required
        dialogInitialFocus
      ></mwc-textfield>
      <mwc-list activatable>
        <mwc-radio-list-item left value="2003"
          >Edition 1 (Schema 1.7)</mwc-radio-list-item
        >
        <mwc-radio-list-item left value="2007B"
          >Edition 2 (Schema 3.1)</mwc-radio-list-item
        >
        <mwc-radio-list-item left selected value="2007B4"
          >Edition 2.1 (2007B4)</mwc-radio-list-item
        >
      </mwc-list>
      <mwc-button
        slot="secondaryAction"
        dialogAction="close"
        label="Cancel"
        style="--mdc-theme-primary: var(--mdc-theme-error)"
      ></mwc-button>
      <mwc-button
        slot="primaryAction"
        icon="create_new_folder"
        label="Create"
        @click="${this.createNewProject}"
      ></mwc-button>
    </mwc-dialog>`;
  }

  render() {
    return html`
      ${this.renderNewDialog()}
      <div class="landing">
        <mwc-icon-button
          id="openproject"
          class="landing_icon"
          icon="folder_open"
          @click="${() => this.input.click()}"
        >
          <div class="landing_label">Open Project</div>
        </mwc-icon-button>
        <mwc-icon-button
          id="newproject"
          class="landing_icon"
          icon="create_new_folder"
          @click="${() => this.newDialog.show()}"
        >
          <div class="landing_label">Open Project</div>
        </mwc-icon-button>
      </div>
      <input
        @click=${({ target }: MouseEvent) => {
          // eslint-disable-next-line no-param-reassign
          (<HTMLInputElement>target).value = '';
        }}
        @change=${this.openDoc}
        type="file"
      />
    `;
  }

  static styles = css`
    :host {
      background-color: green;
    }

    input {
      display: none;
    }

    .landing {
      position: absolute;
      text-align: center;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 100%;
    }

    .landing_icon:hover {
      box-shadow: 0 12px 17px 2px rgba(0, 0, 0, 0.14),
        0 5px 22px 4px rgba(0, 0, 0, 0.12), 0 7px 8px -4px rgba(0, 0, 0, 0.2);
    }

    .landing_icon {
      margin: 12px;
      border-radius: 16px;
      width: 160px;
      height: 140px;
      text-align: center;
      color: var(--mdc-theme-on-secondary);
      background: var(--secondary);
      --mdc-icon-button-size: 100px;
      --mdc-icon-size: 100px;
      --mdc-ripple-color: rgba(0, 0, 0, 0);
      box-shadow: rgb(0 0 0 / 14%) 0px 6px 10px 0px,
        rgb(0 0 0 / 12%) 0px 1px 18px 0px, rgb(0 0 0 / 20%) 0px 3px 5px -1px;
      transition: box-shadow 280ms cubic-bezier(0.4, 0, 0.2, 1);
    }

    .landing_label {
      width: 160px;
      height: 50px;
      margin-top: 100px;
      margin-left: -30px;
      font-family: 'Roboto', sans-serif;
    }
  `;
}
